.wp-block-zolo-advanced-image {
    --zolo-content-padding: 50px;
    --zolo-title-margin: 0;
    --zolo-title-text-color: #fff;
    --zolo-caption-margin: 15px 0 0;
    --zolo-caption-text-color: #d2d4e3;
    --zolo-separator-margin: 10px 0;
    --zolo-separator-width: 180px;
    --zolo-separator-border-width: 2px 0 0;
    --zolo-separator-border-color: #fff;
    --zolo-separator-border-style: solid;
    --zolo-img-overlay-margin: 30px;
    .zolo-image-block-wrap {
        display: flex;
    }
    .zolo-img {
        vertical-align: middle;
        transition: all 0.35s ease-in-out;
        max-width: 100%;
        width: 100%;
        height: 100%;
    }
    .zolo-content-wrap,
    .zolo-arrow-icon {
        display: none;
    }

    .zolo-image-block-inner {
        position: relative;
    }

    .zolo-img-wrap {
        position: relative;
        transition: transform 0.35s ease-in-out;
    }

    /* hover effect (slide-bottom)   */
    .zolo-adi-slide-bottom {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    transform: translateY(-20px);
                }
            }
        }
    }

    /* hover effect (slide-left)   */
    .zolo-adi-slide-left {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    transform: translateX(20px);
                }
            }
        }
    }

    /* hover effect (slide-right)   */
    .zolo-adi-slide-right {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    transform: translateX(-20px);
                }
            }
        }
    }

    /* hover effect (slide-top)   */
    .zolo-adi-slide-top {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    transform: translateY(20px);
                }
            }
        }
    }

    /* hover effect (zoom)  */
    .zolo-adi-zoom {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    transform: scale(1.05);
                }
            }
        }
    }

    /* hover effect (zoom)  */
    .zolo-adi-gray {
        .zolo-img-wrap {
            filter: grayscale(0%);
            transition: 0.35s ease-in-out;
        }
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap {
                    filter: grayscale(100%);
                }
            }
        }
    }

    /* hover effect (blur)   */
    .zolo-adi-blur {
        .zolo-image-block-inner {
            &:hover {
                .zolo-img {
                    filter: blur(3px);
                }
            }
        }
    }

    /* content & overlay css here   */

    .zolo-adi-overlay {
        .zolo-img-wrap {
            /* overlay css here  */
            &::before,
            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }

            &::after {
                margin: var(--zolo-img-overlay-margin);
                z-index: 1;
                transition: all 0.35s ease-in-out;
            }

            &::before {
                z-index: 1;
                opacity: 0.2;
                background: linear-gradient(45deg, black, transparent);
                transition:
                    opacity 0.35s ease-in-out,
                    background 0.35s ease-in-out,
                    backdrop-filter 0.35s ease-in-out,
                    height 0.35s ease-in-out,
                    width 0.35s ease-in-out,
                    transform 0.35s ease-in-out;
            }
        }

        .zolo-content-wrap {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            padding: var(--zolo-content-padding);
            box-sizing: border-box;
        }

        .zolo-title {
            margin: var(--zolo-title-margin);
            color: var(--zolo-title-text-color);
            font-size: 36px;
            transition-delay: 0.5s;
        }
        .zolo-caption {
            margin: var(--zolo-caption-margin);
            color: var(--zolo-caption-text-color);
            transition-delay: 2s;
        }

        .zolo-separator {
            margin: var(--zolo-separator-margin);
            transition-delay: 1.5s;
            span {
                width: var(--zolo-separator-width);
                border-width: var(--zolo-separator-border-width);
                border-style: var(--zolo-separator-border-style);
                border-color: var(--zolo-separator-border-color);
                display: inline-block;
            }
        }

        .hover_visible {
            &.zolo-separator,
            &.zolo-title,
            &.zolo-caption {
                opacity: 0;
            }
        }
        .zolo-title,
        .zolo-caption,
        .zolo-separator {
            transform: translateY(20px);
            transition:
                transform 0.35s cubic-bezier(0.31, 0.67, 0.36, 1),
                opacity 0.35s cubic-bezier(0.31, 0.67, 0.36, 1);
        }
        .zolo-image-block-inner {
            &:hover {
                .zolo-img-wrap::before {
                    opacity: 1;
                }
                .zolo-title,
                .zolo-caption,
                .zolo-separator {
                    transform: translateY(0);
                }
                .hover_visible {
                    &.zolo-caption,
                    &.zolo-separator,
                    &.zolo-title {
                        opacity: 1;
                    }
                }
            }
        }
    }
}
